.wrapper {
    display: flex;
    align-items: center;
    padding: 6px 16px;
    cursor: pointer;
    &:hover {
        background-color: rgba(22, 24, 35, .03);
    }
}

.avatar {
    width: 40px;
    height: 40px;
    border-radius: 50px;
    object-fit: cover;
}

.info {
    flex: 1;
    margin-left: 12px;
}

.name {
    font-size: 1.6rem;
    font-weight: 600;

}

.username {
    font-size: 1.4rem;
    color: rgba(22, 24, 35, .5);
}

.check {
     margin-left: 8px;
     color: rgb(32, 213, 236);

}