.wrapper {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    font-size: 1.6rem;
    font-family: 'ProximaNova', sans-serif;
    font-weight: 700;
    padding: 9px 16px;
    cursor: pointer;
    border-radius: 4px;
    background-color: var(--white);
    border: 1px solid transparent;
    user-select: none;

    + .wrapper {
        // margin-left: 8px;
    }
    &.disable {
        pointer-events: none;
        opacity: 0.5;
    }
}
.title + .icon, 
.icon + .title {
    margin-left: 8px;
}
.icon {
    display: inline-block;
    width: 24px;
    text-align: center;
    line-height: 1;
}
// button types
.rounded {
    border-radius: 999px;
    box-shadow: rgb(0 0 0 / 6%);
    border-color: rgba(22, 24, 35, .12);

    &:hover {
        border-color: rgba(22, 24, 35, .2);
        background-color: rgba(22, 24, 35, .03);
    }
}

.primary {
    color: var(--white);
    background-color: var(--primary);
    border-color: var(--primary);

    &:hover {
        border-color: var(--primary);
        background: linear-gradient(0deg, rgba(0, 0, 0, .06), rgba(0, 0, 0, .06)), #fe2c55;   
    }
}
.outline {
    color: var(--primary);
    border-color: var(--primary);

    &:hover {
        border-color: var(--primary);
        background-color: rgba(254, 44, 85, .06);
    }
}

.text {
    &:hover {
        text-decoration: underline;
    }
}

// disable

// button size
.small {
    min-width: 88px;
    padding: 4px 16px;
}
.large {
    padding: 14px 16px;
    min-width: 140px;
}
